import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  CanLoad,
  Route,
  Router,
  RouterStateSnapshot,
  UrlSegment,
  UrlTree,
} from '@angular/router';
import { ConditionsService } from '../services/conditions.service';
import { Observable } from 'rxjs';
import { first } from 'rxjs/operators';

export type ResolveEntitlements = (entitlementDefinition: string) => Promise<boolean>;
export type RedirectionFactory = (
  resolveEntitlements: ResolveEntitlements,
  route: ActivatedRouteSnapshot | Route,
  state: RouterStateSnapshot | UrlSegment[],
) => string | undefined | Promise<string | undefined> | Observable<string | undefined>;

@Injectable({
  providedIn: 'root',
})
export class EntitlementsGuard implements CanActivate, CanActivateChild, CanLoad {
  constructor(protected readonly conditions: ConditionsService, protected readonly router: Router) {}

  protected readonly userHasEntitlements = async (entitlements: string): Promise<boolean> => {
    try {
      if (!entitlements || (await this.conditions.resolveEntitlements(entitlements))) {
        return true;
      }
    } catch (err) {
      console.error(err);
    }
    return false;
  };

  protected async resolveEntitlements(
    entitlements: string,
    redirectTo?: string | RedirectionFactory,
    route?: ActivatedRouteSnapshot | Route,
    state?: RouterStateSnapshot | UrlSegment[],
  ): Promise<boolean | UrlTree> {
    if (await this.userHasEntitlements(entitlements)) {
      return true;
    }

    if (typeof redirectTo === 'function') {
      redirectTo = await this.getRedirection(redirectTo, route, state);
    }

    if (redirectTo) {
      return this.router.parseUrl(redirectTo);
    }

    return false;
  }

  private getRedirection(
    getRedirectTo: RedirectionFactory,
    route?: ActivatedRouteSnapshot | Route,
    state?: RouterStateSnapshot | UrlSegment[],
  ): string | undefined | Promise<string | undefined> {
    if (!route || !state) {
      return undefined;
    }
    const redirectTo = getRedirectTo(this.userHasEntitlements, route, state);
    if (redirectTo instanceof Observable) {
      return redirectTo.pipe(first()).toPromise();
    }
    return redirectTo;
  }

  canActivateChild(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean | UrlTree> {
    const { entitlements, redirectTo } = childRoute.data;
    return this.resolveEntitlements(entitlements, redirectTo, childRoute, state);
  }

  canLoad(route: Route, segments: UrlSegment[]): Promise<boolean | UrlTree> {
    const entitlements = route.data?.entitlements;
    const redirectTo = route.data?.redirectTo;
    return this.resolveEntitlements(entitlements, redirectTo, route, segments);
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean | UrlTree> {
    const { entitlements, redirectTo } = route.data;
    return this.resolveEntitlements(entitlements, redirectTo, route, state);
  }
}
