import { Injectable, Inject, Optional } from '@angular/core';
import { Observable, of, forkJoin } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { ENTITLEMENTS_CONFIG } from './providers';

type DataItemEntitlementResponse = Array<{
  dataItem: {
    id: string;
    datatype: string;
  };
  permissions: {
    businessFunction: string;
    privileges: string[];
    resource: string;
  }[];
}>;

type EntitlementSummaryResponse = Array<{
  resource: string;
  function: string;
  permissions: { [key: string]: boolean };
}>;

interface Entitlement {
  dataItem?: string;
  resource: string;
  function: string;
  privilege: string;
}

enum LogicalOperator {
  AND = 'AND',
  OR = 'OR',
  NOT = 'NOT',
}

interface Condition extends Entitlement {
  logic: LogicalOperator;
}

interface Resolution {
  value: boolean;
  logic: LogicalOperator;
}

export interface EntitlementsConfig {
  forceResolved?: boolean;
  accessControlBasePath?: string;
  accessControlPath?: string;
}

export const SUMMARY_PERMISSIONS_PATH = '/accessgroups/users/permissions/summary';
export const DATA_ITEMS_PERMISSIONS_PATH = '/accessgroups/users/data-item-permissions';

@Injectable({
  providedIn: 'root',
})
export class ConditionsService {
  private forceResolved: boolean;
  private entitlements$: Observable<Entitlement[]>;

  constructor(
    private readonly http: HttpClient,
    @Optional() @Inject(ENTITLEMENTS_CONFIG) private config: EntitlementsConfig | null,
  ) {
    const basePath = this.config?.accessControlBasePath || '/access-control';
    const path = this.config?.accessControlPath || `/client-api/v2${SUMMARY_PERMISSIONS_PATH}`;

    this.entitlements$ = this.http
      .get<EntitlementSummaryResponse | DataItemEntitlementResponse>(`${basePath}${path}`)
      .pipe(
        map(EntitlementSummaryResponse => {
          if (EntitlementSummaryResponse.length && 'dataItem' in EntitlementSummaryResponse[0]) {
            return this.mapDataItemPermissions(EntitlementSummaryResponse as DataItemEntitlementResponse);
          } else {
            return this.mapPermissionsSummary(EntitlementSummaryResponse as EntitlementSummaryResponse);
          }
        }),
        shareReplay(1),
      );

    this.forceResolved = this.config?.forceResolved || false;
  }

  // map response of getUserPermissionsSummary to flat array of available entitlements
  private mapPermissionsSummary(entitlementsData: EntitlementSummaryResponse): Entitlement[] {
    const entitlements: Entitlement[] = [];

    entitlementsData.forEach(entitlementData => {
      const availablePermissions = Object.entries(entitlementData.permissions).filter(([, value]) => value === true);

      availablePermissions.forEach(([privilege]) => {
        const entitlement = this.removeWhiteSpace({
          resource: entitlementData.resource,
          function: entitlementData.function,
          privilege,
        });

        entitlements.push(entitlement);
      });
    });

    return entitlements;
  }

  // map response of getDataItemPermissionsContext to flat array of available entitlements
  // this endpoint can optionally be configured when needing data-item specific entitlements
  private mapDataItemPermissions(entitlementsData: DataItemEntitlementResponse): Entitlement[] {
    const entitlements: Entitlement[] = [];

    entitlementsData.forEach(entitlementData => {
      entitlementData.permissions.forEach(permission => {
        permission.privileges.forEach(privilege => {
          const entitlement = this.removeWhiteSpace({
            dataItem: entitlementData.dataItem.id,
            resource: permission.resource,
            function: permission.businessFunction,
            privilege,
          });

          entitlements.push(entitlement);
        });
      });
    });

    return entitlements;
  }

  resolveEntitlements(identifier: string): Promise<boolean> {
    if (this.forceResolved) {
      return Promise.resolve(true);
    }

    const conditions = this.createConditions(identifier);

    return this.resolveConditions(conditions).toPromise();
  }

  private parseIdentifier(identifier: string): Entitlement {
    const identifierArray = identifier.split('.').map((str: string) => str.trim());

    if (identifierArray.length < 3 || identifierArray.length > 4) {
      throw Error(`'${identifier}' is not valid entitlement identifier`);
    }

    if (identifierArray.length === 3) {
      return this.removeWhiteSpace({
        dataItem: undefined,
        resource: identifierArray[0],
        function: identifierArray[1],
        privilege: identifierArray[2],
      });
    }

    return this.removeWhiteSpace({
      dataItem: identifierArray[0],
      resource: identifierArray[1],
      function: identifierArray[2],
      privilege: identifierArray[3],
    });
  }

  private resolveConditions(conditions: Array<Condition>): Observable<boolean> {
    if (conditions.length === 0) {
      return of(true);
    }

    const resolutions = conditions.map((condition: Condition) =>
      this.resolveEntitlement(condition).pipe(map((value: boolean) => ({ value, logic: condition.logic }))),
    );

    return forkJoin(resolutions).pipe(map(this.resolveResolutions));
  }

  private resolveResolutions(resolutions: Resolution[]) {
    return resolutions.reduce((final: boolean, resolution: Resolution) => {
      if (resolution.logic === LogicalOperator.AND) {
        return final && resolution.value;
      } else if (resolution.logic === LogicalOperator.NOT) {
        return final && !resolution.value;
      }

      return final || resolution.value;
    }, true);
  }

  private separateLogicalOperators(splitBySpaces: string[]): Array<Condition> {
    const conditions: Array<Condition> = [];

    for (let i = 0; i < splitBySpaces.length; i += 2) {
      const logic = splitBySpaces[i];
      if (logic !== LogicalOperator.AND && logic !== LogicalOperator.OR) {
        throw Error(`'${logic}' is not valid logical operator`);
      }

      const identifier = splitBySpaces[i + 1];
      if (identifier.charAt(0) === '!') {
        conditions.push({
          ...this.parseIdentifier(identifier.substr(1)),
          logic: LogicalOperator.NOT,
        });
      } else {
        conditions.push({
          ...this.parseIdentifier(identifier),
          logic,
        });
      }
    }

    return conditions;
  }

  private removeWhiteSpace<T extends { dataItem?: string; resource: string; function: string }>(item: T): T {
    return {
      ...item,
      dataItem: item.dataItem ? item.dataItem.replace(/ /g, '') : undefined,
      resource: item.resource.replace(/ /g, ''),
      function: item.function.replace(/ /g, ''),
    };
  }

  private createConditions = (stringDef: string): Array<Condition> => {
    const splitBySpaces = stringDef
      .trim()
      .split(' ')
      .filter((spaced: string) => spaced !== '');

    return this.separateLogicalOperators(['AND'].concat(splitBySpaces));
  };

  private resolveEntitlement(entitlement: Entitlement): Observable<boolean> {
    return this.entitlements$.pipe(
      map(entitlements =>
        entitlements.some(
          availableEntitlement =>
            (entitlement.dataItem === undefined || availableEntitlement.dataItem === entitlement.dataItem) &&
            availableEntitlement.resource === entitlement.resource &&
            availableEntitlement.function === entitlement.function &&
            availableEntitlement.privilege === entitlement.privilege,
        ),
      ),
    );
  }
}
